/** @jsxImportSource @emotion/react */

import React from "react";
import { css, Global } from "@emotion/react";
import { Header } from "./Header";
import { Box, Flex } from "reflexbox";
import { useWindowSize } from "./useWindowSize";
import { Router, navigate, useLocation } from "@reach/router";
import { PrettyGradientText } from "./PrettyGradientText";
import { PrintPageBreak } from "./PrintPageBreak";
import "normalize.css/normalize.css";
import "@blueprintjs/icons/lib/css/blueprint-icons.css";
import "@blueprintjs/core/lib/css/blueprint.css";
import "@blueprintjs/popover2/lib/css/blueprint-popover2.css";

import { Portfolio } from "./Portfolio";

import { Resume } from "./Resume";
import { AboutMe } from "./AboutMe";

function PageNav() {
  const location = useLocation();
  return (
    <Flex
      my={4}
      fontSize="2em"
      textAlign="center"
      justifyContent="space-evenly"
      fontWeight="700"
      flexWrap="wrap"
    >
      {[
        {
          caption: "About Me",
          path: "/",
          regex: /\/$/
        },
        {
          caption: "Resume",
          path: "/resume",
          regex: /\/resume/
        },
        {
          caption: "Portfolio",
          path: "/portfolio/all",
          regex: /\/portfolio/
        }
      ].map((nav, indx, arr) => {
        let isSelected = location.pathname.match(nav.regex) !== null;
        return (
          <Box
            width={[1, 1 / arr.length]}
            key={nav.caption}
            color="hsl(200,93%,35%)"
            css={css`
              cursor: pointer;

              transform: scale(1);
              transition: transform 0.2s ease-out, color 0.2s;
              &:hover {
                transform: scale(1.05);

                animation: none;
              }

              &:active {
                color: #fff;
                transform: scale(0.8);
                animation: none;
              }
            `}
            onClick={() => {
              navigate(nav.path);
            }}
          >
            {isSelected ? (
              <PrettyGradientText speed={10}>{nav.caption}</PrettyGradientText>
            ) : (
              nav.caption
            )}
          </Box>
        );
      })}
    </Flex>
  );
}

export default function App() {
  const size = useWindowSize();

  const [printMode, setPrintMode] = React.useState(true);

  let mobileSize = size.width <= 640;
  if (printMode) mobileSize = false;

  return (
    <Box className="App" mb={5}>
      <Global
        styles={css`
          html,
          body {
            background-color: ${printMode ? "white" : "#cef"};
            color: #333;
            font-family: "Maven Pro";
            background-repeat: no-repeat;
            a:link {
              color: #904e95;
            }
            a:visited {
              color: #904e95;
            }
          }
        `}
      />

      <Header {...{ printMode, mobileSize, setPrintMode }} />
      {printMode ? <PrintBody /> : <AppBody {...{ mobileSize }} />}
    </Box>
  );
}

function PrintBody({}) {
  return (
    <Flex>
      <Box>
        <AboutMe printMode />
        <PrintPageBreak />
        <Resume printMode />
        <PrintPageBreak />
        <Portfolio printMode />
      </Box>
    </Flex>
  );
}

function AppBody({ mobileSize }) {
  return (
    <Flex justifyContent="center" flexWrap="wrap">
      <Box width={[1, 0.8, 0.7]}>
        <PageNav />
        <Router primary={false}>
          <Portfolio mobileSize={mobileSize} path="/portfolio/:currentSlug" />
          <Resume mobileSize={mobileSize} path="/resume" />
          <AboutMe default />
        </Router>

        {mobileSize && <PageNav />}
      </Box>
    </Flex>
  );
}
