/** @jsxImportSource @emotion/react */

import { Button, ButtonGroup, Menu, MenuItem } from "@blueprintjs/core";
import { Popover2 } from "@blueprintjs/popover2";
import { GrAscend, GrDescend } from "react-icons/gr";

export function SortControls({ SortKeyState, SortDirState, SearchState }) {
  const [sortKey, setSortKey] = SortKeyState;
  const [sortDir, setSortDir] = SortDirState;
  const [search] = SearchState;

  return (
    <ButtonGroup>
      <Popover2
        disabled={search}
        placement="bottom-end"
        content={
          <Menu>
            <MenuItem
              text="Date"
              data-text="Date"
              active={sortKey === "Date"}
              onClick={() => setSortKey("Date")}
            />
            <MenuItem
              text="Project Name"
              active={sortKey === "Project Name"}
              onClick={() => setSortKey("Project Name")}
            />
          </Menu>
        }
      >
        <Button
          disabled={search}
          text={`Sort by ${sortKey}`}
          intent="primary"
          rightIcon="caret-down"
          minimal
          outlined
        />
      </Popover2>
      <Button
        disabled={search}
        icon={sortDir === "Desc" ? <GrDescend /> : <GrAscend />}
        intent="primary"
        minimal
        outlined
        onClick={() => {
          setSortDir(sortDir === "Desc" ? "Asc" : "Desc");
        }}
      />
    </ButtonGroup>
  );
}
