import { Button, InputGroup } from "@blueprintjs/core";
import { FaTimes } from "react-icons/fa";

export function SearchControls({ SearchState }) {
  const [search, setSearch] = SearchState;
  return (
    <InputGroup
      leftIcon="search"
      onChange={(e) => {
        setSearch(e.target.value);
      }}
      placeholder="Search..."
      value={search}
      rightElement={
        search.length > 0 && (
          <Button
            icon={<FaTimes />}
            intent="danger"
            minimal
            onClick={() => {
              setSearch("");
            }}
          />
        )
      }
    />
  );
}
