/** @jsxImportSource @emotion/react */
import { css } from "@emotion/react";
import { Box } from "reflexbox";
import { PrettyGradientText } from "./PrettyGradientText";

const comicPointer = css`
  &:after {
    content: "";
    position: absolute;
    right: 0;
    top: 50%;
    width: 20px;
    height: 20px;
    background-color: #fff;
    display: block;
    transform: rotate(-45deg);
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
    clip-path: polygon(100% 0, 0% 100%, 100% 100%);
    box-shadow: -1px 1px 2px rgba(0, 0, 0, 0.4);
    margin-top: -10px;
    margin-right: -9px;
  }
`;

export function TitleStatement({ mobileSize, topLine, bottomLine }) {
  return (
    <Box
      p={3}
      my={3}
      fontSize={mobileSize ? "1.3em" : "1.8em"}
      textAlign="center"
      css={css`
        position: relative;
        transform: rotate(-4deg);
        background: #fff;
        border-radius: 0.4em;
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);

        ${!mobileSize && comicPointer}
      `}
    >
      <Box fontWeight="700">
        <PrettyGradientText>{topLine}</PrettyGradientText>
      </Box>
      <Box fontSize=".75em">{bottomLine}</Box>
    </Box>
  );
}
