import { Switch } from "@blueprintjs/core";
import { FaPrint } from "react-icons/fa";
import { Flex, Box } from "reflexbox";
import { AnimatedHead } from "./AnimatedHead";
import { PrettyGradientText } from "./PrettyGradientText";
import { TitleStatement } from "./TitleStatement";

function PrintModeSwitch({ printMode, setPrintMode }) {
  return (
    <Flex justifyContent="flex-end" className="hideOnPrint" flexWrap="center">
      <Box>
        <Switch
          id="printMode"
          checked={printMode}
          onClick={() => {
            setPrintMode(!printMode);
          }}
        />
      </Box>
      <Box>
        <label htmlFor="printMode">
          <FaPrint />
        </label>
      </Box>
    </Flex>
  );
}

export function Header({ mobileSize, printMode, setPrintMode }) {
  return (
    <>
      <Flex justifyContent="center">
        <Box textAlign="right" mb={-50} mt={2} width={[860]}>
          <PrintModeSwitch {...{ printMode, setPrintMode }} />
        </Box>
      </Flex>
      <Flex
        mb={0}
        pb={2}
        flexWrap="wrap"
        alignItems={printMode ? "center" : "flex-end"}
        justifyContent="center"
      >
        {printMode ? (
          <>
            <Box width={"80pt"} mr={2}>
              <AnimatedHead />
            </Box>

            <Box fontSize="1.5em" textAlign="center" width="300pt">
              <NamePlate printMode />
            </Box>
            <Box width="80pt" />
          </>
        ) : (
          <>
            {" "}
            <Box zIndex="90" width={[5 / 6, 1 / 3]}>
              <TitleStatement
                {...{ mobileSize }}
                topLine="Hey Planning Center!"
                bottomLine="I want to work with you!"
              />
            </Box>
            <Box zIndex="100" mt={["-25pt", 0]} width={[2 / 5, 1 / 5, 1 / 6]}>
              <AnimatedHead />
            </Box>
            <Box width={[5 / 6, 1 / 3]}>
              <NamePlate />
            </Box>{" "}
          </>
        )}
      </Flex>
    </>
  );
}

function NamePlate({ printMode }) {
  return (
    <Box mb={0} fontSize="1.4em">
      <Box fontWeight={700}>
        <span style={{ color: "#504E95" }}>
          <PrettyGradientText {...{ printMode }}>
            Tim Anderson
          </PrettyGradientText>
        </span>
      </Box>
      <Box opacity=".7" fontSize=".8em">
        Web Developer
      </Box>
      <Box opacity=".7" mt={2} fontSize="11pt">
        <a href="mailto:vtowntim@gmail.com">vtowntim@gmail.com</a>
        <br />
        <a href="tel:15599097677">(559) 909-7677</a>
      </Box>
    </Box>
  );
}
