import moment from "moment";
import React from "react";
import lodash from "lodash";
import { projectLibrary } from "./projectLibrary";
import { quickScore } from "quick-score";
import ColorHash from "color-hash";

const slugify = (text) =>
  text
    .toString()
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, "")
    .toLowerCase()
    .trim()
    .replace(/\s+/g, "-")
    .replace(/[^\w-]+/g, "")
    .replace(/--+/g, "-");

const organizations = ["Kaweah Health", "Alameda Health System"];

const technologies = [
  "Adobe Captivate",
  "ASP.NET MVC",
  "C#",
  "Church Center",
  "CSS",
  "HTML",
  "Javascript",
  "Moodle",
  "MySQL",
  "NodeJS",
  "PHP",
  "Publishing",
  "React",
  "SharePoint",
  "SQL",
  "Websockets",
  "XML"
];

let colorHash = new ColorHash({ lightness: 0.6, saturation: 0.7 });

let projectLibraryManipulated = projectLibrary.map((p) => {
  p.color = colorHash.hex(p.name);
  p.tech = p.tech.sort((a, b) => a.localeCompare(b));
  p.slug = slugify(p.name);
  return p;
});

export function useProjects() {
  const SortKeyState = React.useState("Date");
  const [sortKey] = SortKeyState;

  const SortDirState = React.useState("Desc");
  const [sortDir] = SortDirState;

  const SearchState = React.useState("");
  const [search] = SearchState;

  const SelectedTechState = React.useState([]);
  const [selectedTech] = SelectedTechState;

  let projects = projectLibraryManipulated;

  if (selectedTech.length) {
    projects = projects.filter((p) => {
      return lodash.difference(selectedTech, p.tech).length === 0;
    });
  }

  if (search.length) {
    projects = projects
      .map((p) => {
        p.rank = quickScore(p.name + "" + p.desc, search);
        return p;
      })
      .filter((p) => {
        return p.rank > 0.18;
      })
      .sort((a, b) => b.rank - a.rank);
  } else {
    projects = projects.sort((a, b) => {
      if (sortKey === "Date") {
        return moment(a.date).valueOf() - moment(b.date).valueOf();
      } else {
        return a.name.localeCompare(b.name);
      }
    });

    if (sortDir !== "Asc") projects = [...projects].reverse();
  }

  return {
    projects,
    organizations,
    technologies,
    SearchState,
    SortKeyState,
    SortDirState,
    SelectedTechState
  };
}
