/** @jsxImportSource @emotion/react */
import React from "react";
import { Box, Flex } from "reflexbox";
import { css } from "@emotion/react";
import { Button, Classes, Collapse, Tag } from "@blueprintjs/core";
import moment from "moment";
import { WorkHistoryOrganizations } from "./WorkHistoryOrganizations";
import {
  FaAdobe,
  FaApple,
  FaCaretDown,
  FaCaretRight,
  FaMicrosoft
} from "react-icons/fa";
import { PrettyGradientText } from "./PrettyGradientText";
import { KnowledgeDomains } from "./KnowledgeDomains";
import { SectionHeader } from "./SectionHeader";

export function Resume({ mobileSize, printMode }) {
  return (
    <Box
      css={css`
        background: white;
      `}
      p={3}
      mx={printMode && 4}
      className={!printMode && Classes.ELEVATION_1}
    >
      <section>
        <SectionHeader>Employment History</SectionHeader>

        {WorkHistoryOrganizations.map((organization) => {
          return (
            <WorkHistoryItem
              key={organization.name}
              {...organization}
              {...{ mobileSize, printMode }}
              org={organization.name}
            />
          );
        })}
      </section>

      <section>
        <SectionHeader>Knowledge and Experience</SectionHeader>
        {KnowledgeDomains.map((domain, ind) => {
          let DomainIcon = null;
          if (domain.title === "Apple") DomainIcon = <FaApple />;
          if (domain.title === "Adobe") DomainIcon = <FaAdobe />;
          if (domain.title === "Microsoft") DomainIcon = <FaMicrosoft />;

          return (
            <Box mb={printMode ? 3 : 4} key={domain.title}>
              <Flex justifyContent="center" alignItems="baseline">
                {DomainIcon && <Box mr={1}>{DomainIcon}</Box>}
                <Box fontSize={"1.4em"} textAlign="center">
                  {domain.title}
                </Box>
              </Flex>
              {domain.skills.length > 0 && (
                <Flex flexWrap="wrap" mx={["2pt", 5]} justifyContent="center">
                  {domain.skills.map((skill) => {
                    return (
                      <Box p={1} key={skill}>
                        <Tag minimal intent="primary">
                          {skill}
                        </Tag>
                      </Box>
                    );
                  })}
                </Flex>
              )}
              {domain.desc && (
                <Box mx={printMode ? 5 : [1, 6]}>{domain.desc}</Box>
              )}
            </Box>
          );
        })}
      </section>
    </Box>
  );
}

/*
{
  title: '',
  dept: '',
  start: null,
  end: null,
  duties: ''
}
*/

function WorkHistoryItem({
  org,
  location,
  jobs,
  years,
  future,
  mobileSize,
  printMode
}) {
  return (
    <Box
      mb={future ? 3 : 4}
      p={future && 2}
      css={
        future &&
        css`
          border-radius: 6pt;
          border: 1pt solid hsl(50, 100%, 65%);
        `
      }
      backgroundColor={future && "hsl(50,100%,98%)"}
    >
      <Flex flexWrap="wrap" alignItems="baseline">
        <Box fontWeight={future ? "700" : "500"} fontSize="1.6em">
          {future ? (
            <span style={{ color: "#904E95" }}>
              <PrettyGradientText speed={2} {...{ printMode }}>
                {org}
              </PrettyGradientText>
            </span>
          ) : (
            org
          )}
        </Box>
        <Box
          ml={printMode ? 3 : [0, 3]}
          width={printMode ? "auto" : [1, "auto"]}
          className={Classes.TEXT_MUTED}
          fontStyle="italic"
        >
          {location}
        </Box>
        <Box flex={1} />
        <Box className={Classes.TEXT_MUTED} textAlign="right">
          {years}
        </Box>
      </Flex>
      <Box mx={printMode ? 3 : [0, 3]}>
        {jobs.map((job) => {
          return (
            <JobItem
              {...{ job, future, mobileSize, printMode }}
              key={job.title}
            />
          );
        })}
      </Box>
    </Box>
  );
}

function JobItem({ job, future, mobileSize, printMode }) {
  const [isOpen, setIsOpen] = React.useState(printMode ? true : false);

  let $start = moment(job.start);

  let formattedDateStart = $start.format("MMM 'YY");

  let $end = moment(job.end);

  let formattedDateEnd = $end.format("MMM 'YY");

  if (!job.end) formattedDateEnd = "Present";

  return (
    <Box my={mobileSize ? 4 : 3} key={job.title}>
      <Flex
        css={
          !future &&
          css`
            cursor: pointer;
          `
        }
        flexWrap="wrap"
        alignItems="baseline"
        onClick={() => setIsOpen(!isOpen)}
      >
        <Flex alignItems="baseline">
          {!future && (
            <Box width="3em">
              <Button
                minimal
                small
                icon={
                  printMode ? (
                    <FaCaretRight />
                  ) : isOpen ? (
                    <FaCaretDown />
                  ) : (
                    <FaCaretRight />
                  )
                }
                intent={"primary"}
              />
            </Box>
          )}

          <Box fontSize="1.2em">
            {future ? (
              <span style={{ color: "#904E95" }}>
                <PrettyGradientText speed={2} {...{ printMode }}>
                  {job.title}
                </PrettyGradientText>
              </span>
            ) : (
              job.title
            )}
          </Box>
        </Flex>
        <Box
          ml={printMode ? 2 : [0, 2]}
          width={printMode ? "auto" : [1, "auto"]}
          className={Classes.TEXT_MUTED}
        >
          {job.dept}
        </Box>
        <Box flex={1} />
        <Box
          width={printMode ? "auto" : [1, "auto"]}
          className={Classes.TEXT_MUTED}
          textAlign={mobileSize ? "left" : "right"}
        >
          {future ? (
            <span style={{ color: "#904E95" }}>
              <PrettyGradientText speed={2} {...{ printMode }}>
                <span
                  css={css`
                    font-weight: 900;
                  `}
                >
                  THE FUTURE!
                </span>
              </PrettyGradientText>
            </span>
          ) : (
            <span>
              {formattedDateStart}{formattedDateEnd}
            </span>
          )}
        </Box>
      </Flex>

      {!future && (
        <Collapse {...{ isOpen }}>
          <Box
            my={2}
            ml={printMode ? 4 : [1, 4]}
            css={
              printMode &&
              css`
                font-size: 9pt;
              `
            }
          >
            Duties: {job.duties}
          </Box>
        </Collapse>
      )}
    </Box>
  );
}
