/** @jsxImportSource @emotion/react */
import { Box, Flex } from "reflexbox";
import { css } from "@emotion/react";
import { Button, Classes, Overlay, Tag } from "@blueprintjs/core";
import moment from "moment";
import React from "react";
import { FaTimes } from "react-icons/fa";
import colorJS from "color";
import { navigate } from "@reach/router";

function TechCloud({ tech, printMode, selectedTech = [] }) {
  return (
    <Flex justifyContent="center" flexWrap="wrap">
      {tech.map((technology) => {
        let isSelected = selectedTech.includes(technology);
        return (
          <Box key={technology} p={1}>
            <Tag
              intent={isSelected ? "primary" : "none"}
              css={
                printMode &&
                css`
                  font-size: 8pt !important;
                `
              }
              minimal
            >
              {technology}
            </Tag>
          </Box>
        );
      })}
    </Flex>
  );
}

function PrintModeProject({
  project,
  showRank,
  currentSlug,
  SelectedTechState,
  printMode
}) {
  let { name, slug, color, org, desc, date, rank, tech, screenshots } = project;
  let $date = moment(date);
  let formattedDate = $date.format("MMM 'YY");
  let firstScreenshot = screenshots[0];
  return (
    <Flex className="dontBreakOnPrint" mb={4} width={1}>
      <Box width={1 / 4}>
        <Flex
          height={150}
          css={css`
            border-radius: 3pt;
            background: linear-gradient(
                hsla(200, 95%, 60%, 0.02) 20%,
                ${colorJS(color)
                  .fade(0.6)
                  .desaturate(0.6)
                  .darken(0.2)
                  .toString()}
              ),
              url(${firstScreenshot});
            background-size: cover;
            background-position: center;
            border-bottom: 2pt solid ${color};
            /* min-height: 200px; */
            /* padding-top: 56.25%; */
            /* width: 100%; */
            color: white;
          `}
          flexDirection="column"
        >
          <Box
            // backgroundColor="yellow"
            alignSelf="flex-end"
            m={1}
            ml={2}
            textAlign="right"
          >
            <Box
              px={2}
              fontWeight="500"
              fontSize="10pt"
              css={css`
                background: ${colorJS(color)
                  .fade(0.2)
                  .desaturate(0.2)
                  .darken(0.2)
                  .toString()};
                border-radius: 4pt;
              `}
            >
              {formattedDate}
            </Box>
          </Box>
          {/* <Flex
            p={2}
            flex={1}
            width={1}
            fontSize="2.2em"
            fontWeight="bold"
            // backgroundColor="red"
            alignSelf="flex-start"
            alignItems="flex-end"
            css={css`
              text-shadow: 0 1px 2px hsla(0, 0%, 0%, 0.6);
            `}
          >
            {/* <Box
            // backgroundColor="blue"
            >
              {name}
            </Box> 
          </Flex> */}
        </Flex>
      </Box>
      <Box flex={1} mx={4}>
        <Box fontSize="14pt" fontWeight="500">
          {name}
        </Box>
        <Box>{desc}</Box>
      </Box>
      <Box width={1 / 4}>
        <TechCloud {...{ tech, printMode }} />
      </Box>
    </Flex>
  );
}

export function Project({
  project,
  showRank,
  currentSlug,
  SelectedTechState,
  printMode
}) {
  if (printMode)
    return (
      <PrintModeProject
        {...{ project, showRank, currentSlug, SelectedTechState, printMode }}
      />
    );

  let { name, slug, color, org, desc, date, rank, tech, screenshots } = project;

  let isOpen = currentSlug === slug;

  const [selectedTech] = SelectedTechState;

  let $date = moment(date);

  let formattedDate = $date.format("MMM 'YY");
  let formattedDateLonger = $date.format("MMMM YYYY");

  let firstScreenshot = screenshots[0];

  let technologyCloud = <TechCloud {...{ tech, selectedTech }} />;

  return (
    <Box className="dontBreakOnPrint" width={printMode ? 1 / 2 : [1, 1 / 2]}>
      <Box
        my={2}
        p={2}
        mx={printMode ? 3 : 2}
        backgroundColor="white"
        onClick={() => {
          // setIsOpen(true);
          navigate(`${slug}`);
        }}
        className={
          !printMode && Classes.ELEVATION_2 + " " + Classes.INTERACTIVE
        }
        css={css`
          border-radius: 3px;
          cursor: pointer;
          /* box-shadow: 0 0 0 1px rgb(16 22 26 / 10%),
            0 1px 1px rgb(16 22 26 / 20%), 0 2px 6px rgb(16 22 26 / 20%);
          transition: transform 0.2s cubic-bezier(0.4, 1, 0.75, 0.9),
            box-shadow 0.2s cubic-bezier(0.4, 1, 0.75, 0.9),
            -webkit-transform 0.2s cubic-bezier(0.4, 1, 0.75, 0.9),
            -webkit-box-shadow 0.2s cubic-bezier(0.4, 1, 0.75, 0.9); */
          &:hover {
            /* box-shadow: 0 0 0 1px rgb(16 22 26 / 10%),
              0 2px 4px rgb(16 22 26 / 20%), 0 8px 24px rgb(16 22 26 / 20%); */

            transform: scale(1.02);
            animation: none;
          }

          transform: scale(1);
          transition: transform 0.2s ease-out;

          &:active {
            transform: scale(0.95);
            animation: none;
          }
        `}
      >
        <Flex
          css={css`
            border-radius: 3pt;
            background: linear-gradient(
                hsla(200, 95%, 60%, 0.05) 20%,
                ${colorJS(color)
                  .fade(0.2)
                  .desaturate(0.6)
                  .darken(0.2)
                  .toString()}
              ),
              url(${firstScreenshot});
            background-size: cover;
            background-position: center;
            border-bottom: 2pt solid ${color};
            min-height: 200px;
            /* padding-top: 56.25%; */
            /* width: 100%; */
            color: white;
          `}
          flexDirection="column"
        >
          <Box
            // backgroundColor="yellow"
            alignSelf="flex-end"
            fontWeight="normal"
            m={1}
            ml={2}
            textAlign="right"
          >
            <Box
              px={2}
              css={css`
                background: ${colorJS(color)
                  .fade(0.2)
                  .desaturate(0.2)
                  .darken(0.2)
                  .toString()};
                border-radius: 4pt;
              `}
            >
              {formattedDate}
            </Box>
          </Box>
          <Flex
            p={2}
            flex={1}
            fontSize="2.2em"
            fontWeight="bold"
            // backgroundColor="red"
            alignSelf="flex-start"
            alignItems="flex-end"
            css={css`
              text-shadow: 0 1px 2px hsla(0, 0%, 0%, 0.6);
            `}
          >
            <Box
            // backgroundColor="blue"
            >
              {name}
            </Box>
          </Flex>
        </Flex>
        {showRank && (
          <Box p={1} textAlign="center">
            {Math.round(rank * 100)}% Match
          </Box>
        )}

        <Box
          p={2}
          className={[
            "bp3-text-small",
            !printMode && "bp3-text-overflow-ellipsis"
          ].join(" ")}
        >
          {desc}
        </Box>
        {/* <Box>{org}</Box> */}

        {/* <Flex justifyContent="center" flexWrap="wrap">
          {screenshots.slice(0, 1).map((s) => (
            <Box key={s.substr(-8)} p={1}>
              <img src={s} height="100px" />
            </Box>
          ))}
        </Flex> */}

        <Box mx={2} height="4em">
          {technologyCloud}
        </Box>
      </Box>
      <Overlay
        isOpen={isOpen}
        onClose={() => {
          navigate(`all`);
        }}
      >
        <Flex
          onClick={() => {
            navigate(`all`);
          }}
          alignItems="center"
          justifyContent="center"
          width={1}
        >
          <Box
            onClick={(e) => {
              e.stopPropagation();
              e.preventDefault();
            }}
            textAlign="center"
            p={2}
            width={0.9}
            css={css`
              margin-top: 5vh;
              background-color: white;
              border-radius: 3pt;
              background: linear-gradient(
                hsla(200, 0%, 100%, 0.95) 40%,
                ${colorJS(color).mix(colorJS("white"), 0.7).toString()} 90%
              );
            `}
            className={[Classes.ELEVATION_4].join(" ")}
          >
            <Box py={2}>
              <Flex>
                <Box width={50} />
                <Box
                  flex={1}
                  color={colorJS(color).desaturate(0.3).darken(0.3).toString()}
                  fontSize="2.8em"
                  fontWeight="700"
                >
                  {name}
                </Box>{" "}
                <Box width={50}>
                  <Button
                    icon={<FaTimes />}
                    minimal
                    onClick={() => {
                      navigate(`all`);
                    }}
                  />
                </Box>
              </Flex>

              <Flex justifyContent="center" className="bp3-text-muted">
                <Box>{org}</Box>
                <Box mx={2} />
                <Box>{formattedDateLonger}</Box>
              </Flex>
            </Box>

            <Box
              css={css`
                overflow-y: auto;
                max-height: 60vh;
              `}
              justifyContent="center"
              flexWrap="wrap"
            >
              {technologyCloud}

              <Box my={3} p={2}>
                {desc}
              </Box>

              {screenshots.map((s) => (
                <Box
                  key={s.substr(-8)}
                  m={4}
                  css={css`
                    background: linear-gradient(
                        ${colorJS(color)
                          .fade(0.1)
                          .desaturate(0.8)
                          .darken(0.5)
                          .toString()},
                        ${colorJS(color)
                          .fade(0.1)
                          .desaturate(0.8)
                          .darken(0.5)
                          .toString()}
                      ),
                      url(${firstScreenshot});
                    background-position: center;
                    background-size: 150%;
                  `}
                >
                  <Box
                    css={css`
                      border: 1px solid #ccc;
                      border-radius: 3pt;
                      background: url(${s});
                      background-size: contain;
                      background-repeat: no-repeat;
                      background-position: center;

                      /* min-height: 600px; */
                      padding-top: 56.25%;
                      width: 100%;
                      color: white;
                    `}
                  />
                  {/* <img src={s} width="100%" /> */}
                </Box>
              ))}
            </Box>
          </Box>
        </Flex>
      </Overlay>
    </Box>
  );
}
