/** @jsxImportSource @emotion/react */

import lodash from "lodash";
import { Box, Flex } from "reflexbox";
import { Tag } from "@blueprintjs/core";
import { FaTimes } from "react-icons/fa";

export function TechnologyFilterControls({ technologies, SelectedTechState }) {
  const [selectedTech, setSelectedTech] = SelectedTechState;

  return (
    <Box my={2}>
      <Box textAlign="center" className="bp3-text-muted">
        Filter by technology
      </Box>
      <Flex justifyContent="center">
        <Flex
          width={[1, 1, 2 / 3]}
          my={1}
          justifyContent="center"
          flexWrap="wrap"
        >
          {technologies.map((technology) => {
            let isSelected = selectedTech.includes(technology);
            return (
              <Box m={1} key={technology}>
                <Tag
                  active={isSelected}
                  interactive
                  minimal
                  intent={isSelected ? "primary" : "none"}
                  rightIcon={isSelected && <FaTimes />}
                  onClick={() => {
                    if (!isSelected) {
                      setSelectedTech([...selectedTech, technology]);
                    } else {
                      setSelectedTech(lodash.without(selectedTech, technology));
                    }
                  }}
                >
                  {technology}
                </Tag>
              </Box>
            );
          })}
        </Flex>
      </Flex>
    </Box>
  );
}
