/** @jsxImportSource @emotion/react */
import { Classes } from "@blueprintjs/core";
import { css } from "@emotion/react";
import { Box, Flex } from "reflexbox";
import { Link } from "@reach/router";
import { PrettyGradientText } from "./PrettyGradientText";
import Elders from "./assets/Elders.jpeg";
import Eliza from "./assets/Eliza.jpeg";
import Married from "./assets/Married.jpg";
import Preaching from "./assets/Preaching.JPG";
import colorJS from "color";
import { SectionHeader } from "./SectionHeader";
import { PrintPageBreak } from "./PrintPageBreak";

export function AboutMe({ printMode }) {
  return (
    <Box
      backgroundColor="white"
      mx={printMode && 4}
      className={!printMode && Classes.ELEVATION_1}
    >
      <Box p={3}>
        <SectionHeader>About Me</SectionHeader>
      </Box>

      <Section>
        <Box
          p={printMode ? 4 : [3, 4]}
          mx={printMode ? 5 : [0, 3, 5, 6]}
          css={css`
            & p {
              margin-bottom: 2em;
            }
          `}
        >
          <Title>I'm ready to join your team!</Title>
          <p>
            Hi! I'm{" "}
            <strong>
              <PrettyGradientText {...{ printMode }}>
                Tim Anderson
              </PrettyGradientText>
            </strong>
            , a web developer from Visalia, California{" "}
            <em>
              (about 5 hours north of Carlsbad, between Bakersfield and Fresno)
            </em>
            . I've been coding since my mom brought home our first IBM PC in
            1991, and I never want to stop! I've dabbled in many types of
            artistic expressions throughout my life, such as theatre, painting,
            and writing, but creating applications and experiences in a text
            editor has been the most fulfilling for me by far.
          </p>
          <p>
            If you take a look at my{" "}
            <Link to="resume">
              <PrettyGradientText {...{ printMode }}>resume</PrettyGradientText>
            </Link>
            , you'll see that my career was forged in the healthcare industry. I
            started answering calls at my local Hospital's Service Desk in 2007.
            From there, my ability to connect with users, a little bit of
            entrepreneurial spirit, plus my capabilities in web development gave
            me many unique opportunities to expand my skillset and experience.
          </p>
          <p>
            <Link to="portfolio/all">
              <PrettyGradientText {...{ printMode }}>
                My portfolio
              </PrettyGradientText>
            </Link>{" "}
            contains over 30 different applications that I have developed since
            2011. In both of the hospitals I worked at, we didn't have
            development teams, just me and sometimes one other database analyst
            using our skills to solve problems. I was often brought in to help
            create a custom solution in order to save the hospital time and
            resources. Medical documentation systems, on-call calendar
            solutions, data dashboards, full-featured time management trackers,
            these are just some of the applications I developed over the years.
            In many of the cases, I was responsible for the whole stack.
          </p>

          <p>
            <strong>
              I've been a grateful customer of Planning Center since 2014
            </strong>
            . In my previous church, I was the point-person to implement your
            powerful tools. We began with{" "}
            <PrettyGradientText {...{ printMode }}>Services</PrettyGradientText>{" "}
            and soon-after implemented{" "}
            <PrettyGradientText {...{ printMode }}>
              Check-Ins
            </PrettyGradientText>{" "}
            and then eventually every other application. I've been watching your
            careers page for some time, waiting for the right job-posting to be
            available, and I believe this is it! While I have abilities to work
            a full-stack, the front-end is my wheelhouse. And although I
            appeciate the sense of purpose I get from helping nurses and doctors
            do their work, I would love to be part of an organization that is
            helping churches. This is literally dream-job material.
          </p>
        </Box>
      </Section>

      <PrintPageBreak />

      <Box px={3} pb={3}>
        <SectionHeader>Let's Get Personal</SectionHeader>
      </Box>
      <Section>
        <Flex flexWrap="wrap-reverse" alignItems="center">
          <Box p={printMode ? 3 : [1, 3]} width={printMode ? 0.5 : [1, 1 / 2]}>
            <Title>I'm a husband</Title>
            <p>
              I've been married to Cheryl for 7 years; whenever I tell our
              story, I make sure people know my wife is the evidence of God's
              grace in my life. She seems to complement every part of me, and
              together we're able to dream big and actually make things happen.
            </p>
          </Box>
          <Box width={printMode ? 0.5 : [1, 1 / 2]}>
            <Photo url={Married} aspectRatio={printMode ? null : 1} />
          </Box>
        </Flex>

        <Flex flexWrap="wrap" alignItems="center">
          <Box width={printMode ? 0.5 : [1, 1 / 2]}>
            <Photo url={Eliza} aspectRatio={printMode ? null : 1} />
          </Box>
          <Box p={printMode ? 3 : [1, 3]} width={printMode ? 0.5 : [1, 1 / 2]}>
            <Title>I'm a daddy</Title>
            <p>
              My daughter Eliza{" "}
              <small>
                <em>
                  (Yes, she's kinda named after Eliza Hamilton... Yes, because
                  of the musical, but also: it's a pretty name)
                </em>
              </small>{" "}
              just turned three years-old, and I couldn't be prouder. Of all my
              apps and creations, she's the best thing I've made yet. She is
              smart, funny, and adorable. When I start at Planning Center, I'll
              have to bring her sometime so you can see <em>*why*</em> I work so
              hard.
            </p>
          </Box>
        </Flex>

        <Flex flexWrap="wrap-reverse" alignItems="center">
          <Box p={printMode ? 3 : [1, 3]} width={printMode ? 0.5 : [1, 1 / 2]}>
            <Title>I'm also a pastor</Title>
            <p>
              My wife and a few team members felt God call us to start a new
              church in September 2019. Echo Church was born in my living room,
              but only stayed there for two weeks. Our numbers grew quicker than
              we exptected, so much of 2019 and 2020 was us trusting God as we
              meet at a local hotel, then finally settled into our downtown
              building.
            </p>
          </Box>
          <Box width={[printMode ? 0.5 : 1, 1 / 2]}>
            <Photo url={Preaching} aspectRatio={printMode ? null : 1} />
          </Box>
        </Flex>

        <Flex flexWrap="wrap" alignItems="center">
          <Box width={printMode ? 0.5 : [1, 1 / 2]}>
            <Photo url={Elders} aspectRatio={printMode ? null : 1} />
          </Box>
          <Box p={printMode ? 3 : [1, 3]} width={printMode ? 0.5 : [1, 1 / 2]}>
            <Title>I'm bivocational</Title>
            <p>
              While I am the Lead Pastor of Echo Church, I have a dynamic Elder
              leadership that allows me to work full-time. Many of the duties
              that normally fall to a Lead Pastor have been delegated to the
              four other Elders in my church, and we have been blessed for it.
              This is working surprisingly well for us. I usually preach four
              Sundays in a row, and take two off, which allows my Associate
              Pastors to serve in the pulpit, and gives me regular breaks.
            </p>
          </Box>
        </Flex>
      </Section>
    </Box>
  );
}

function Section({ children, printMode }) {
  return <Box mb={5}>{children}</Box>;
}

function Title({ children }) {
  return (
    <Box fontWeight="700" fontSize="1.6em">
      {children}
    </Box>
  );
}

function Photo({ url, color, aspectRatio }) {
  if (!color) color = "hsl(200,50%,50%)";
  if (!aspectRatio) aspectRatio = 9 / 16;

  // Aspect Ratio 16:9 -- 9/16

  return (
    <Box
      css={css`
        background: linear-gradient(
            ${colorJS(color).fade(0.1).desaturate(0.8).darken(0.5).toString()},
            ${colorJS(color).fade(0.1).desaturate(0.8).darken(0.5).toString()}
          ),
          url(${url});
        background-position: center;
        background-size: 150%;
        border-radius: 3pt;
      `}
    >
      <Box
        css={css`
          border: 1px solid #ccc;
          border-radius: 3pt;
          background: url(${url});
          background-size: contain;
          background-repeat: no-repeat;
          background-position: center;

          /* min-height: 600px; */
          padding-top: ${aspectRatio * 100}%;
          width: 100%;
          color: white;
        `}
      />
    </Box>
  );
}
